@charset "UTF-8";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: StratosSkyeng, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('/assets/fonts/stratosskyengweb-regular.woff2') format('woff2');
}

body {
    font-family: StratosSkyeng, sans-serif;
    font-style: normal;
    background: #004980;
    max-width: 1024px;
    max-height: 774px;
    margin-left: auto;
    margin-right: auto;
}

.window {
    margin-left: auto;
    margin-right: auto;
    background: #c2f5ff;
    border-radius: 12px;
    height: 459px;
    top: 158px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 480px;
    position: relative;
}

.window__title {
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    font-size: 40px;
    line-height: 48px;
    text-align: center;
    color: #004980;
    margin-top: 52px;
    margin-left: 136px;
    margin-right: 136px;
}

.window__level {
    margin-top: 48px;
    display: flex;
    margin-right: 10px;
}

.window__level input[type='radio'] {
    display: none;
}

.window__level label {
    cursor: pointer;
    width: 98px;
    height: 98px;
    background: #fff;
    border-radius: 12px;
    margin-left: 12px;
    margin-right: 12px;
    border-color: transparent;
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 72px;

    /* or 112% */
    display: flex;
    align-items: center;
    justify-content: center;
    color: #0080c1;
}

/* Hover */
.window__level label:hover {
    color: #004980;
}

/* Checked */
.window__level input[type='radio']:checked + label {
    background: #efefef;
    color: #004980;
}

.game__button {
    width: 246px;
    height: 50px;
    margin-top: 67px;
    background: #7ac100;
    border-radius: 12px;
    border: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
}

.game__button:hover {
    color: #004980;
}

.game-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 24px;
    margin-right: 24px;
    margin-top: 24px;
}

.game-header__timer {
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 72px;

    /* identical to box height, or 112% */
    color: #fff;
}

.game-header__timer_header {
    display: flex;
    justify-content: flex-end;
    font-size: 16px;
    line-height: 32px;
    gap: 40%;
}

.restart__button {
    width: 246px;
    height: 48px;
    background: #7ac100;
    border-radius: 12px;
    border: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
}

.restart__button:hover {
    color: #004980;
}

.game__field {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
    margin-top: 30px;
}

.game__card_start {
    width: 95px;
    height: 133px;
    background: #fff;
    border-radius: 4px;
}

.memory-card {
    background: #fff;
    border-radius: 4px;
    width: 95px;
    height: 133px;
    position: relative;
    box-shadow: 1px 1px 0 rgb(0 0 0 / 30%);
    transform-style: preserve-3d;
    transition: transform 0.5s;
}

.memory-card.flip {
    transform: rotateY(180deg);
}

.memory-card:active {
    transform: scale(0.97);
    transition: transform 0.2s;
}

.front-face,
.back-face {
    position: absolute;
    background: #1c7ccc;
    backface-visibility: hidden;
}

.front-face {
    transform: rotateY(180deg);
}

.icon-game {
    margin-top: 32px;
}

.window__message {
    font-size: 40px;
    line-height: 48px;
    color: #004980;
}

.window__timer {
    font-size: 24px;
    line-height: 32px;
    color: #000;
    margin-top: 28px;
}

.window__time {
    font-size: 64px;
    line-height: 72px;
    color: #000;
    margin-top: 10px;
}

.end {
    margin-top: 48px;
}

/* настройки затемнения при выводе модального окна */
.modal-overlay {
    /* затемняем экран */
    background: rgb(0 73 128);

    /* располагаем окно по центру экрана */
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.9;
}

.the-end {
    margin-left: auto;
    margin-right: auto;
    background: #c2f5ff;
    top: -100px;
    border-radius: 12px;
    height: 459px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 480px;
    position: relative;
}
